import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    productList: [], // 
    bargainList: [], // 
    specialList: [], // 
    popularitylList: [], // 
    particularsList: [], // 
    quanList: [], // 
    commList: [], // 
    partId: '', // Id
    token: {}, // 
    barinId: '', // ID
    barId: '',
    bainfo: [], // 
    helps: [], // 
    num: 1, // 
    gouwuList: [], // 
    qxcheck: { check: true }, // 
    dele: false,
    dizhilist: [], // 
    moren: [], // 
    ordernumber: [], // 
    shunum: 0,
    logstatus: false, // 
    orderid: '', // ID
    orderxq: [], // 
    order: [] // 

  },
  mutations: {
    // 
    fan (state, obj) {
      state.token = obj
      // this.$store.state.token = JSON.parse(window.localStorage.getItem('1902'))
      console.log(state.token)

      // state.token = obj
    },
    // 
    gouwulist (state, val) {
      let index = state.gouwuList.findIndex(item => {
        return item.colorid === val.colorid && item.sizeid === val.sizeid
      })
      if (index === -1) {
        state.gouwuList.push(val)
      } else {
        state.gouwuList[index].num++
      }
      // this.$store.state.gouwuList = JSON.parse(window.localStorage.getItem('gouwu'))
      console.log(state.gouwuList)
    },
    // 
    xuan (state) {
      console.log(1)
      let arr = state.gouwuList.every(item => {
        return item.checked === true
      })
      console.log(arr)
      if (arr) {
        state.qxcheck.check = true
      } else {
        state.qxcheck.check = false
      }
    },
    // 
    qcheck (state) {
      state.qxcheck.check = !state.qxcheck.check
      state.gouwuList.map(item => {
        item.checked = !item.checked
      })
    },
    // 
    accom (state) {
      state.qxcheck.check = !state.qxcheck.check
      state.gouwuList.map(item => {
        item.checked = false
      })
      state.dele = true
    },
    // 
    editor (state) {
      state.qxcheck.check = !state.qxcheck.check
      state.gouwuList.map(item => {
        item.checked = true
      })
      state.dele = false
    },
    // 
    deleted (state) {
      let arr = []
      state.gouwuList.filter((item, index) => {
        if (item.checked === false) {
          arr.push(item)
        }
      })
      state.gouwuList = arr
    }
  },
  getters: {
    // 
    nums (state) {
      let shunum0 = 0
      state.gouwuList.map(item => {
        shunum0 += item.num
        // console.log(numss1)
      })
      return shunum0
    },
    // 
    prices (state) {
      let pricess = 0
      state.gouwuList.map(item => {
        if (item.checked === true) {
          pricess += item.num * item.barinfo.basicInfo.minPrice
          // console.log(pricess)
        }
      })
      return pricess
    }
  }
})
export default store



